  border-bottom-right-radius: 3px;
  padding: 10px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Hiragino Sans GB", "Microsoft Yahei", "微软雅黑", Arial, Helvetica, STHeiti, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 18px;
  line-height: 1.2;
  color: #333;
  z-index: 1; }

.CodeMirror-scroll {
  min-height: 300px;
  margin-bottom: -30px;
  margin-right: -30px;
  padding-bottom: 30px; }

.CodeMirror-fullscreen {
  background: #fff;
  position: fixed !important;
  top: 58px;
  left: 0;
  right: 0;
  bottom: 0;
  height: auto;
  z-index: 9;
  border: 0;
  border-top: 1px solid #ddd; }

.CodeMirror-sided {
  width: 50% !important; }

.CodeMirror pre {
  padding: 0 4px; }

